$width100: 100%;

@mixin mixinflex {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}


html, body {
  font-family: 'Arial', sans-serif;
  font-size: 10px;

}

body {
  @include mixinflex;
  background: linear-gradient(-45deg, #ee7752, #e73c7e, #23a6d5, #23d5ab);
  background-size: 400% 400%;
  animation: gradient 15s ease infinite;
  width: $width100;
  height: 100vh;
}

@keyframes gradient {
  0% {
    background-position: 0 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0 50%;
  }
}

.container {
  @include mixinflex;
  width: $width100 - 10%;
  text-transform: uppercase;
  height: 95%;
  gap: 3%;

}

.header {
  font-size: 3rem;
  color: white;
}


.category_box {
  font-size: 0.5rem;
  height: 100%;
  width: $width100;
  display: grid;
  grid-template-columns: 1fr 1fr ;
  gap: 0.5rem;

}

.category_button {
  height: 100%;
  font-size: 2rem;
  margin-bottom: 5px;
  background: none;
  border: 2px solid white;
  border-radius: 5px;
  color: white;
}

.answer_box:after {
  background-color: white;
  transition: background-color
}

